// -----------------------------
// Video Background Home

.video-bg {
	position: relative;
	height: 300px;
	@include bp(sm) {
		height: 400px;
	}
	@include bp(xx) {
		height: 500px;
	}
}

.video-bg video {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	object-fit: cover;
	z-index: 5;
	// opacity: 0.8;
}
.video-banner__container {
	position: relative;
	display: flex;
	justify-content: flex-end;
	padding-top: $spacer * 2;
}
.video-banner__btns {
	display: none;
	@include bp(md) {
		display: flex;
		flex-direction: column;
		position: absolute;
		z-index: 10;
		// top: 0;
		// right: 0;
		p {
			margin-bottom: $spacer;
		}

		a {
			width: 100%;
			text-align: center;
		}
	}
}
