// -----------------------------
// eBook Request

.ebook-cta {
	padding-top: $spacer * 2;
	padding-bottom: $spacer * 2;
	background-color: $green;
	color: $white;
	@include bp(md) {
		padding-top: $spacer * 3;
		padding-bottom: $spacer * 3;
	}
	@include bp(xl) {
		padding-top: $spacer * 4;
		padding-bottom: $spacer * 4;
	}
}

.ebook-cta--container {
	display: flex;
	flex-direction: column;
	@include bp(md) {
		flex-direction: row;
		flex-wrap: wrap;
		align-items: flex-end;
		justify-content: flex-end;
	}
	@include bp(lg) {
		justify-content: space-between;
	}
}

.ebook-cta__img {
	// flex: 0 1 200px;
	margin: 0 auto $spacer * 2;

	@include bp(md) {
		margin: 0 $spacer * 2 0 0;
		flex: 0 1 100px;
	}
	@include bp(lg) {
		margin: 0;
		flex: 0 1 200px;
	}
	img {
		transform: rotate(-5deg);
		box-shadow: 0 0.8px 1.5px rgba(0, 0, 0, 0.125),
			0 6px 12px rgba(0, 0, 0, 0.25);
		max-width: 160px;
		@include bp(md) {
			max-width: 100px;
		}
		@include bp(md) {
			max-width: 160px;
		}
	}
}
.ebook-cta__content {
	margin-bottom: $spacer;
	flex: 1;
	@include bp(md) {
		flex: 0 1 70%;
	}
	@include bp(lg) {
		flex: 1;
		margin-bottom: 0;
	}
	.ebook-cta__heading {
		color: $white;
		margin-top: 0;
	}
	p {
		@include font-size(16);
		margin-bottom: $spacer/2;
	}
	& *:last-child {
		margin-bottom: 0;
	}
}

.ebook-cta__form {
	flex: 1;
	@include bp(md) {
		// flex-basis: 30%;
		flex: 0 1 70%;
		align-self: flex-end;
	}
	@include bp(lg) {
		flex: 1;
		margin-left: $spacer * 2;
	}
}
