// -----------------------------
// Products

//  Products -- Simple grid
.products-simple {
	list-style: none;
	padding: 0;
	margin: 0;

	a {
		text-decoration: none;
		color: $black;
	}

	display: flex;
	flex-wrap: wrap;
	// justify-content: space-between;
	justify-content: center;
	// align-content: center;

	li {
		flex: 1 1 50%;
		text-align: center;
		padding: $spacer 0;
		@include bp(md) {
			padding: $spacer * 2 0;
			flex: 0 1 33%;
		}
	}
}

.products-simple__btn {
	margin: 0 auto $spacer/2;
	background-color: blue;
	border-radius: 50%;
	width: 85px;
	height: 85px;
	@include bp(lg) {
		width: 105px;
		height: 105px;
	}

	display: flex;
	justify-content: center;
	align-items: center;

	transition: $transition;

	.icon {
		width: 60px;
		height: 60px;
		@include bp(lg) {
			width: 80px;
			height: 80px;
		}
	}
}

.products-simple__title {
	color: $grey;
	font-weight: $font__medium;
	@include bp(lg) {
		@include font-size(18);
	}
}

// Products Detailed
.products-detailed {
	list-style: none;
	padding: 0;
	margin: 0;

	a {
		text-decoration: none;
		color: $black;
	}

	@include bp(md) {
		display: flex;
		flex-wrap: wrap;
		justify-content: center;
		// align-items: center;
	}

	li {
		flex: 1 1 50%;
		text-align: center;
		padding: 0 $spacer * 2 $spacer * 1.5;
		border-bottom: $border;
		margin-bottom: $spacer * 2;

		&:last-child {
			border-bottom: none;
			margin-bottom: 0;
			padding-bottom: 0;
		}
		@include bp(md) {
			max-width: 50%;
			padding: 0 $spacer * 2 $spacer * 2.5;
			margin-bottom: $spacer * 3;

			&:nth-child(5) {
				border-bottom: none;
				margin-bottom: 0;
				padding-bottom: 0;
			}
		}
		@include bp(lg) {
			padding-left: $spacer * 4;
			padding-right: $spacer * 4;
		}
		@include bp(xx) {
			padding-left: $spacer * 8;
			padding-right: $spacer * 8;
		}
		@include bp(xxx) {
			padding-left: $spacer * 10;
			padding-right: $spacer * 10;
		}
	}

	.icon {
		width: 40px;
		height: 40px;
		margin-right: $spacer;
	}
}

.products-detailed__btn {
	display: flex;
	margin: 0 auto;
	align-items: center;
	max-width: 220px;
	border-radius: $radius;
	padding: $spacer/2 $spacer;
	margin-bottom: $spacer * 1.5;
	transition: $transition;
	@include bp(xl) {
		max-width: 240px;
	}

	a & {
		color: $white;
	}
}
.products-detailed__title {
	margin: 0;
	@include font-size(22);
	@include bp(xl) {
		@include font-size(26);
	}
}
.products-detailed__overview {
	margin-bottom: $spacer/2;
}
.products-detailed__more {
	margin-bottom: 0;
}

// Button Colours
.products__btn--referral {
	background-color: $referral--c;

	.icon {
		color: lighten($referral--c, 10%);
	}

	&:hover {
		background-color: darken($referral--c, 10%);
