// -----------------------------
// Module Spacing

.module-spacing--none {
	margin-top: 0;
	margin-bottom: 0;
}
.module-spacing--before {
	margin-top: $spacer * 2;
	margin-bottom: 0;
	@include bp(md) {
		margin-top: $spacer * 3;
	}
	@include bp(xl) {
		margin-top: $spacer * 4;
	}
}
.module-spacing--after {
	margin-top: 0;
	margin-bottom: $spacer * 2;
	@include bp(md) {
		margin-bottom: $spacer * 3;
	}
	@include bp(xl) {
		margin-bottom: $spacer * 4;
	}
}
.module-spacing--both {
	margin-top: $spacer * 2;
	margin-bottom: $spacer * 2;
	@include bp(md) {
		margin-top: $spacer * 3;
		margin-bottom: $spacer * 3;
	}
	@include bp(xl) {
		margin-top: $spacer * 4;
		margin-bottom: $spacer * 4;
	}
}
