// -----------------------------
// Animations & Transitions

$transition: all 0.3s ease-in-out;

// Selective animations from Animate.css - http://daneden.me/animate

// Fade In
@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.fade-in {
  animation-name: fadeIn;
}

// Fade In Down
@keyframes fadeInDown {
  0% {
    opacity: 0;
    transform: translate3d(0, -30px, 0);
  }
  100% {
    opacity: 1;
    transform: none;
  }
}

.fade-in-down {
  animation-name: fadeInDown;
}

// Fade In Down Big
@keyframes fadeInDownBig {
  0% {
    opacity: 0;
    transform: translate3d(0, -100%, 0);
  }
  100% {
    opacity: 1;
    transform: none;
  }
}

.fade-in-down-big {
  animation-name: fadeInDownBig;
}

