// Project Colors
$black: #000;
$white: #fff;
$grey: rgba(109, 110, 113, 1);

$blue--dark: rgba(0, 49, 70, 1);
$blue--mid: rgb(26, 106, 137);
$blue--light: rgba(209, 225, 231, 1);

$red: rgba(237, 83, 56, 1); // #ed5338
$green: rgba(88, 185, 71, 1); // #58b947
$purple: rgba(148, 52, 115, 1); // #943473
$aqua: rgba(0, 164, 199, 1); // #00a4c7
$orange: rgba(249, 157, 42, 1); // #f99d2a

$referral--c: $red;
$cases--c: $green;
$clients--c: $aqua;
$community--c: $purple;
$staff--c: $orange;
$bespoke--c: $grey;

// Brand Colors
$color__primary: $blue--dark;
$color__secondary: $blue--mid;
$color__tertiary: $red;
$color__neutral: $blue--light;

// Default Colors
$color__background-body: #fff;
$color__link: $blue--mid;
$color__link-visited: $blue--mid;
$color__link-hover: $blue--dark;
$color__text-main: $grey;
