// Button reset
button {
	border: none;
	background-color: transparent;
	padding: 0;
	&:hover,
	&:active,
	&:focus {
		border-color: transparent;
	}
}

.btn,
a.btn,
input[type="button"],
input[type="reset"],
input[type="submit"] {
	display: inline-block;
	border: 1px solid;
	border-color: $color__secondary;
	border-radius: $radius;
	background: $color__secondary;
	color: $white;
	// @include font-size(12);
	line-height: 1;
	padding: $spacer/2 $spacer;
	text-decoration: none;
	transition: $transition;

	&:visited {
		color: $white;
	}

	&:hover,
	&:active,
	&:focus {
		border-color: $color__primary;
		background: $color__primary;
		color: $white;
	}
}

.btn--alt,
a.btn--alt {
	border-color: $color__tertiary;
	background: $color__tertiary;
	&:hover,
	&:active,
	&:focus {
		border-color: darken($color__tertiary, 15%);
		background: darken($color__tertiary, 15%);
	}
}
.btn--dark,
a.btn--dark {
	border-color: $color__primary;
	background: $color__primary;
	padding: $spacer $spacer * 2;
	&:hover,
	&:active,
	&:focus {
		border-color: darken($color__tertiary, 15%);
		background: darken($color__tertiary, 15%);
	}
}
.btn--green,
a.btn--green {
	border-color: $green;
	background: $green;
	color: $white;

	&:visited {
		color: $white;
	}

	&:hover,
	&:active,
	&:focus {
		border-color: $color__secondary;
		background: $color__secondary;
		color: $white;
	}
}
