input[type="text"],
input[type="email"],
input[type="url"],
input[type="password"],
input[type="search"],
input[type="number"],
input[type="tel"],
input[type="range"],
input[type="date"],
input[type="month"],
input[type="week"],
input[type="time"],
input[type="datetime"],
input[type="datetime-local"],
input[type="color"],
textarea,
select {
	color: $color__primary;
	border: $border;
	border-radius: $radius;
	padding: 6px 8px;
	padding: 6px 8px !important;

	&:focus {
		color: #111;
	}
}

select {
	border: 1px solid #ccc;
	padding: 6px 8px !important;
	width: 100%;
	color: $color__secondary;
}

textarea {
	width: 100%;
}
label {
	font-size: 0.9rem !important;
}

// Styles placeholder text
::-webkit-input-placeholder {
	color: $color__secondary;
}
:-moz-placeholder {
	/* Firefox 18- */
	color: $color__secondary;
}
::-moz-placeholder {
	/* Firefox 19+ */
	color: $color__secondary;
}
:-ms-input-placeholder {
	color: $color__secondary;
}
