// -----------------------------
// Footer
.footer {
	background-color: $color__secondary;
	padding-top: $spacer * 4;
	// padding-bottom: $spacer * 4;
	color: $white;
	@include font-size(14);
}

// Layout
.footer-details {
	display: flex;
	flex-direction: column;

	@include bp(sm) {
		.footer__contacts {
			display: flex;
			justify-content: space-between;
		}
		.footer__contacts--location {
			padding-right: $spacer;
		}
	}

	@include bp(md) {
		flex-direction: row;
		flex-wrap: wrap;
		justify-content: space-between;
		align-items: flex-end;

		.footer__branding {
			flex: 1 0 100%;
		}

		.footer__contacts--location {
			padding-right: $spacer * 1.5;
		}
	}

	@include bp(xl) {
		// justify-content: flex-start;
		.footer__contacts--location {
			padding-right: $spacer * 2;
		}
	}

	@include bp(xx) {
		.footer__branding {
			flex: 1 0 200px;
		}
	}
	@include bp(xxx) {
		.footer__branding {
			flex: 0 0 440px;
		}
	}
}

// Branding
.footer__branding {
	// outline: 1px dotted red;
	margin-bottom: $spacer * 1.5;
	@include bp(xx) {
		margin-bottom: 0;
	}
}
.footer__title {
	display: inline-block;
	background-image: url(img/ecins-logo--white.png);
	background-image: linear-gradient(transparent, transparent),
		url(img/ecins-logo--white.svg);
	background-repeat: no-repeat;
	background-size: contain;
	text-indent: 100%;
	white-space: nowrap;
	overflow: hidden;
	margin: 0;
	padding: 0;
	width: 160px;
	height: 30px;
	@include bp(lg) {
		width: 200px;
		height: 37px;
	}
	@include bp(xx) {
		width: 240px;
		height: 45px;
	}
}
.footer__tagline {
	margin-top: $spacer/2;
	margin-bottom: 0;
	@include bp(xx) {
		margin-top: $spacer;
	}
}

// Locations & Contact
.footer__contacts {
	article {
		// outline: 1px dotted lime;
		margin-bottom: $spacer * 1.5;
		@include bp(lg) {
			margin-bottom: 0;
		}
	}

	h4 {
		margin: 0;
		@include font-size(16);
		font-weight: $font__bold;
	}
	p {
		margin: 0;
	}

	a {
		color: $white;
	}
}

.footer__contacts--direct {
	// @include bp(sm) {
	// 	h4 {
	// 		margin-bottom: $spacer * 1.3;
	// 	}
	// }
}

// Subscribe
.footer__subscribe {
	text-align: center;
	margin-bottom: $spacer * 2;
	@include bp(xxx) {
		margin-bottom: $spacer * 4;
	}
}

.footer--secondary {
	padding-top: $spacer * 2;
	padding-bottom: $spacer * 2;
	@include font-size(14);

	display: flex;
	flex-direction: column;
	@include bp(md) {
		flex-direction: row;
		justify-content: space-between;
		align-items: center;
	}
	@include bp(lg) {
		padding-top: $spacer * 3;
	}
	a {
		color: $white;
	}
}
.copyright {
	order: 3;
	margin-bottom: $spacer/2;
	@include bp(md) {
		order: 1;
		margin-bottom: 0;
	}
}

.footer-navigation {
	order: 2;
	margin: $spacer 0;
	li {
		display: inline-block;
		margin-right: $spacer;
		@include bp(md) {
			margin-left: $spacer/2;
			margin-right: $spacer/2;
		}
	}
}

// Social
.footer__social {
	order: 1;
	@include bp(md) {
		order: 3;
	}
	li {
		margin-right: $spacer;
		@include bp(md) {
			margin-left: $spacer/2;
			margin-right: 0;
			// transform: translateY(1px);
		}
		@include bp(xxx) {
			margin-left: $spacer;
		}
	}
}
