// -----------------------------
// Main Navigation

// Mobile menu
.main-navigation--mob {
	ul {
		clear: both;
		list-style: none;
		padding: 0;
	}
	a {
		border-bottom: 1px solid rgba(255, 255, 255, 0.2);
		color: $color__neutral;

		a:visited {
			color: $color__neutral;
		}
		&:hover,
		&:focus,
		&:active {
			// color: $color__link-hover;
			background-color: $color__secondary;
		}
		&:focus {
			outline: thin dotted;
		}
		&:hover,
		&:active {
			outline: 0;
		}
	}
	.current_page_item > a,
	.current-menu-item > a,
	.current_page_ancestor > a,
	.current-menu-ancestor > a {
		background-color: $color__secondary;
	}

	// .request-demo,
	// .download-ebook {
	// 	margin: $spacer/3 $spacer;
	// 	width: 100%;
	// 	@include font-size(14);
	// }

	// .client-login {
	// 	margin: $spacer/3 $spacer;
	// 	a {
	// 		width: 100%;
	// 		@include font-size(14);
	// 	}
	// }
}

.offcanvas-search {
	margin: 0 $spacer $spacer * 1.5;

	input[type="search"] {
		margin-bottom: $spacer/4;
	}

	input[type="search"],
	input[type="submit"] {
		width: 100%;
		@include font-size(16);
	}
}

.offcanvas-cta {
	margin: 0 $spacer;
	p {
		margin: 0;
	}
	a.request-demo,
	a.request-ebook {
		margin: $spacer/2 0;
		text-align: center;
		width: 100%;
		@include font-size(16);
		border-bottom: none;
		color: $white;
	}

	.client-login {
		margin: $spacer/2 0;
		a {
			width: 100%;
			@include font-size(16);
			text-align: center;
		}
	}
}

// Menu Toggles
.menu-toggle {
	padding: 0;
	display: block;

	@include bp(lg) {
		display: none;
	}

	.icon {
		@include font-size(26);
	}
}
.menu-toggle--close {
	float: right;
	// outline: 1px dotted lime;
	margin-top: $spacer * 2;
	margin-right: $spacer/1.8;
	color: $white;
	@include bp(lg) {
		display: none;
	}
}

// Desktop Nav
.main-navigation {
	display: none;
	@include bp(lg) {
		display: block;
	}

	ul {
		list-style: none;
		margin: 0;
		padding-left: 0;

		ul {
			// box-shadow: 0 3px 3px rgba(0, 0, 0, 0.2);
			float: left;
			position: absolute;
			top: 100%;
			left: -999em;
			z-index: 99999;
			background-color: $color__neutral;
			padding: $spacer/2 0;
			border-top: 28px solid $white;

			ul {
				left: -999em;
				top: 0;
			}

			li {
				&:hover > ul,
				&.focus > ul {
					left: 100%;
				}
			}

			a {
				width: 200px;
				// padding: $spacer/4 0;
				padding: $spacer/4 $spacer/2;
			}

			:hover > a,
			.focus > a {
			}

			a:hover,
			a.focus {
				background-color: $color__secondary;
				color: $white;
			}
		}

		li:hover > ul,
		li.focus > ul {
			left: auto;
		}
	}

	#primary-menu > li {
		margin-left: $spacer * 1.5;
		@include bp(xl) {
			margin-left: $spacer * 2;
		}
	}
	li {
		float: left;
		position: relative;
		// margin-left: $spacer * 2;

		&:hover > a,
		&.focus > a {
		}
	}

	a {
		display: block;
		text-decoration: none;
		text-transform: lowercase;
		color: $grey;
		&:visited {
			color: $grey;
		}
		&:hover,
		&:focus,
		&:active {
			color: $color__secondary;
			text-decoration: none;
		}
		&:focus {
			outline: thin dotted;
		}
		&:hover,
		&:active {
			outline: 0;
		}
	}

	.current_page_item > a,
	.current-menu-item > a,
	.current_page_ancestor > a,
	.current-menu-ancestor > a {
		color: $color__secondary;
	}
}
