ul,
ol {
  margin: 0 0 $spacer * 1.5 0;
}

ul {
  list-style: disc;
}

ol {
  list-style: decimal;
}

li > ul,
li > ol {
  margin-bottom: 0;
  margin-left: $spacer * 1.5;
}

dt {
  font-weight: bold;
}

dd {
  margin: 0 $spacer * 1.5 $spacer * 1.5;
}

// Reset lists in Nav
nav > ul {
  list-style: none;
  margin: 0;
  padding: 0;
}
