// -----------------------------
// Page intro block

.page-intro {
	color: $grey;
	font-weight: $font__light;
	text-align: center;
	@include font-size(20);
	@include bp(sm) {
		@include font-size(22);
	}
	@include bp(lg) {
		@include font-size(26);
	}
	@include bp(xl) {
		@include font-size(28);
	}
	// @include bp(xxx) {
	// 	@include font-size(40);
	// }

	*:last-child {
		margin-bottom: 0;
	}
}
