// -----------------------------
// Layout

.wrapper,
.wrapper--outer,
.wrapper--inner,
.wrapper--narrow {
	margin: 0 auto;
	padding-left: $spacer;
	padding-right: $spacer;
	@include bp(sm) {
		padding-left: $spacer * 2;
		padding-right: $spacer * 2;
	}
	@include bp(md) {
		padding-left: $spacer * 3;
		padding-right: $spacer * 3;
	}
	@include bp(xl) {
		padding-left: $spacer * 4;
		padding-right: $spacer * 4;
	}
}

.wrapper {
	max-width: 1600px;
	@include bp(xxxx) {
		padding-left: 0;
		padding-right: 0;
	}
}

.wrapper--inner {
	max-width: 1200px;
	// margin: 0 auto;
	// padding-left: $spacer;
	// padding-right: $spacer;
}

.wrapper--narrow {
	max-width: 960px;
}

@import "grid";
