// -----------------------------
// Locations

.location__item {
	@include bp(md) {
		display: flex;
		justify-content: space-between;
	}
	// align-items: center;
	padding-bottom: $spacer * 2;
	margin-bottom: $spacer * 2;
	border-bottom: $border;

	&:last-child {
		border-bottom: none;
	}
}

.location__content {
	flex: 0 1 30%;
}

.location__map {
	flex: 0 1 62%;
}

.location__title {
	text-align: left;
	margin-top: 0;
}

.location__additionals {
	@include font-size(14);
}
