// -----------------------------
// Testimonials

.testimonials {
	background-color: $color__neutral;
	padding-top: $spacer * 2;
	padding-bottom: $spacer * 2;
	@include bp(md) {
		padding-top: $spacer * 3;
		padding-bottom: $spacer * 3;
	}
	@include bp(xl) {
		padding-top: $spacer * 4;
		padding-bottom: $spacer * 4;
	}
}

.testimonial__item {
	@include bp(md) {
		display: flex;
		align-items: center;
		justify-content: center;
	}
}

.testimonial__quote {
	line-height: 1.2;
	font-weight: $font__light;
	font-style: italic;
	color: $color__secondary;
	@include font-size(20);
	@include bp(sm) {
		@include font-size(22);
	}
	@include bp(lg) {
		@include font-size(26);
	}
	@include bp(xx) {
		@include font-size(28);
	}
}
.testimonial__cite {
	margin: 0;
	color: $color__secondary;
}

.testimonial__img {
	margin-bottom: $spacer;
	@include bp(md) {
		flex: 1;
		margin-right: $spacer;
	}
	@include bp(lg) {
		margin-right: $spacer * 2;
	}
}
.testimonial__content {
	@include bp(sm) {
		flex: 2;
		margin-left: $spacer;
	}
	@include bp(lg) {
		flex: 1;
		margin-left: $spacer * 2;
	}
}
