// -----------------------------
// Grid
.grid {
  overflow: hidden;
  list-style: none;
  padding: 0;
  margin: 0 $spacer/2 $spacer * 2 0;
  @supports (display: grid) {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    grid-gap: $spacer;
    @include bp(md) {
      grid-template-columns: repeat(2, 1fr);
      grid-gap: $spacer * 2;
    }
    @include bp(xl) {
      grid-template-columns: repeat(3, 1fr);
    }
    @include bp(xx) {
      grid-template-columns: repeat(4, 1fr);
    }
    @include bp(xxxx) {
      grid-template-columns: repeat(5, 1fr);
    }
  }
}
.grid__item {
  margin-bottom: $spacer * 2;
  @supports (display: grid) {
    overflow: hidden;
    margin-bottom: 0;
  }
  @include bp(md) {
    float: left;
    width: 48%;
    margin-right: 4%;
    @supports (display: grid) {
      float: none;
      width: auto;
      margin-right: 0;
    }
    &:nth-child(2n) {
      margin-right: 0;
    }
    &:nth-child(2n + 1) {
      clear: left;
      @supports (display: grid) {
        clear: none;
      }
    }
  }
  @include bp(xl) {
    width: 30.666666666666667%;
    margin-right: 4%;
    @supports (display: grid) {
      width: auto;
      margin-right: 0;
    }

    &:nth-child(2n) {
      margin-right: 4%;
      @supports (display: grid) {
        margin-right: 0;
      }
    }
    &:nth-child(2n + 1) {
      clear: none;
    }
    &:nth-child(3n) {
      margin-right: 0;
    }
    &:nth-child(3n + 1) {
      clear: left;
      @supports (display: grid) {
        clear: none;
      }
    }
  }
  @include bp(xx) {
    width: 22%;
    margin-right: 4%;
    @supports (display: grid) {
      width: auto;
      margin-right: 0;
    }

    &:nth-child(3n) {
      margin-right: 4%;
      @supports (display: grid) {
        margin-right: 0;
      }
    }
    &:nth-child(3n + 1) {
      clear: none;
    }
    &:nth-child(4n) {
      margin-right: 0;
    }
    &:nth-child(4n + 1) {
      clear: left;
      @supports (display: grid) {
        clear: none;
      }
    }
  }
  @include bp(xxxx) {
    width: 16.8%;
    margin-right: 4%;
    @supports (display: grid) {
      width: auto;
      margin-right: 0;
    }

    &:nth-child(4n) {
      margin-right: 4%;
      @supports (display: grid) {
        margin-right: 0;
      }
    }
    &:nth-child(4n + 1) {
      clear: none;
    }
    &:nth-child(5n) {
      margin-right: 0;
    }
    &:nth-child(5n + 1) {
      clear: left;
      @supports (display: grid) {
        clear: none;
      }
    }
  }
}
