// -----------------------------
// News and Case Studies

// Listing

.posts__list {
}

.posts__item {
	margin-bottom: $spacer * 2.5;
	padding-bottom: $spacer * 2.5;
	border-bottom: $border;
	@include bp(sm) {
		display: flex;
		justify-content: space-between;
		align-items: center;
	}

	&:last-child {
		margin-bottom: 0;
		border-bottom: none;
	}
}

.posts__content {
	@include bp(sm) {
		padding-left: $spacer * 2.5;
		flex: 1;
	}
	@include bp(md) {
		flex: 5;
	}
	@include bp(lg) {
		flex: 6;
		padding-left: $spacer * 4.5;
	}
}
.posts__img {
	flex: 1;
	@include bp(md) {
		flex: 2;
	}
}

.posts__title {
	a {
		color: $black;
		text-decoration: none;
		// transition: $tran;

		&:visited {
			color: $black;
		}
		&:hover,
		&:focus,
		&:active {
			color: $color__link-hover;
		}
		&:focus {
			outline: thin dotted;
		}
		&:hover,
		&:active {
			outline: 0;
		}
	}
}

// Single Post

.post__header {
	background-color: $color__neutral;
	padding-top: $spacer * 2;
	padding-bottom: $spacer * 2;
}
.post__subtitle {
	color: $color__secondary;
}
.post__img {
	margin-bottom: $spacer * 1.5;
}

// Layout
.site-main--single {
	margin-bottom: $spacer * 2;
	@include bp(lg) {
		margin-bottom: $spacer * 4;
	}
}
.post__content-wrapper {
	@include bp(lg) {
		display: flex;
		justify-content: space-between;
	}
}
.post__main {
	flex: 0 1 75%;
}
.post__sidebar {
	flex: 0 1 20%;
}

.post__sidebar {
	padding-top: $spacer;
	border-top: 3px solid $color__secondary;

	.heading {
		color: $color__secondary;
		margin-top: 0;
	}

	ul {
		list-style: none;
		margin: 0;
		padding: 0;
	}

	li {
		border-bottom: $border;
	}
	a {
		display: block;
		padding: $spacer/1.5 0;
		text-decoration: none;
	}
}

.post__back-btn {
	margin-top: $spacer;
	@include font-size(16);
	text-transform: uppercase;

	.icon {
		transform: translateY(2px);
	}
}
