// -----------------------------
// Offering Product

.offering-product__item {
	@include bp(sm) {
		display: flex;
		justify-content: space-between;
		align-items: center;
	}

	margin-bottom: $spacer * 3;
	padding-bottom: $spacer * 3;
	border-bottom: $border;

	&:last-child {
		border-bottom: none;
	}

	.icon__offerings {
		@include font-size(40);
		color: $color__secondary;
		margin-right: $spacer;
		flex: 0 0 40px;
	}
}

.offering-product__img {
	margin-bottom: $spacer;
	@include bp(sm) {
		margin-bottom: 0;
		flex: 0 1 30%;
	}
}
.offering-product__content {
	flex: 0 1 62%;
}

.offering-product__header {
	display: flex;
	align-items: center;
}
.offering-product__details {
	margin-bottom: 0;
}
