// -----------------------------
// Hero Banner block

.hero-banner {
	background-repeat: no-repeat;
	background-position: center;
	background-size: cover;
	padding-top: $spacer * 4;
	height: 340px;
	@include bp(sm) {
		height: 400px;
	}

	.hero-banner__title {
		margin: 0;
	}
}

.page-template-product .hero-banner {
	height: 260px;
	@include bp(sm) {
		height: 300px;
	}
}

.hero-banner__container {
	@include bp(sm) {
		display: flex;
		flex-direction: row;
		justify-content: space-between;
	}
}

.hero-banner__btns {
	display: none;
	@include bp(md) {
		display: flex;
		flex-direction: column;
	}

	p {
		margin-bottom: $spacer;
	}

	a {
		width: 100%;
		text-align: center;
	}
}
.page-id-13557 {
	p.request-demo {
		display: none;
	}
}

.hero-banner__demo {
	margin-bottom: $spacer;
}
