/* Dots */

.slick-dotted.slick-slider {
	margin-bottom: $spacer;
}

.slick-dots {
	position: absolute;
	bottom: -$spacer * 1.5;
	left: 0;
	list-style: none;
	display: block;
	text-align: center;
	padding: 0;
	margin: 0;
	width: 100%;
	li {
		position: relative;
		display: inline-block;
		height: 10px;
		width: 10px;
		margin: 0 $spacer/2;
		padding: 0;
		cursor: pointer;
		button {
			background: rgba(255, 255, 255, 0.1);
			display: block;
			height: 10px;
			width: 10px;
			outline: none;
			line-height: 0px;
			font-size: 0px;
			color: transparent;
			padding: 0;
			border: 1px solid $white;
			border-radius: 50%;
			cursor: pointer;
			transition: $transition;
			&:hover,
			&:focus {
				outline: none;
				background-color: rgba(255, 255, 255, 1);
			}
		}
		&.slick-active button {
			background-color: rgba(255, 255, 255, 1);
		}
	}
}
