// -----------------------------
// Header

.site-header {
	padding-bottom: $spacer;
	display: flex;
	justify-content: space-between;
	align-items: center;
	position: relative;
	padding-top: $spacer * 2;
	@include bp(lg) {
		padding-top: $spacer * 4;
	}
}
.site-branding {
	flex: 1;
}
.site-title {
	display: inline-block;
	background-image: url(img/ecins-logo.png);
	background-image: linear-gradient(transparent, transparent),
		url(img/ecins-logo.svg);
	background-repeat: no-repeat;
	background-size: contain;
	text-indent: 100%;
	white-space: nowrap;
	overflow: hidden;
	margin: 0;
	padding: 0;
	width: 160px;
	height: 30px;
	@include bp(lg) {
		width: 200px;
		height: 37px;
	}
	@include bp(xx) {
		width: 240px;
		height: 45px;
	}
}

// Client Login
.header-utilities {
	position: absolute;
	z-index: 999;
	top: 0;
	left: 0;
	right: 0;
	display: none;

	@include bp(lg) {
		display: flex;
		// justify-content: space-between;
		justify-content: flex-end;
		padding: 0 $spacer * 3;
	}
	@include bp(xl) {
		padding: 0 $spacer * 4;
		// right: $spacer * 4;
	}
	.search--header {
		// margin-right: $spacer;

		input {
			@include font-size(14);
			border-radius: 0 0 $radius $radius;
			border-top-color: $white;
			// border-right: $border;
		}
		input[type="submit"] {
			border-top-color: $blue--dark;
			@include font-size(14);
			transform: translateY(-1px);
		}
	}
	.client-login {
		&:before {
			content: "|";
			margin: 0 $spacer/2.5 0 $spacer/1.8;
		}
		a {
			border-radius: 0 0 $radius $radius;
			@include font-size(14);
		}
	}
}
