// -----------------------------
// Offering Simple

.offering-simple__list {
	list-style: none;
	margin: 0;
	padding: 0;

	@include bp(sm) {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
	}

	color: $color__secondary;
}

.offering-simple__item {
	flex: 1 0 30%;
	text-align: center;
	padding: $spacer * 1.2 $spacer * 2;
	// max-width: 300px;
	@include bp(sm) {
		padding: $spacer * 2;
	}

	h3 {
		font-weight: $font__medium;
		margin: $spacer/2 0 0;
	}
	.icon__offerings {
		@include font-size(40);
	}
}

// Background

.offering__bg--color {
	background-color: $color__neutral;
	padding: $spacer 0;
}

.offering__bg--lines {
	.offering-simple__title {
		padding-top: $spacer * 2;
		border-top: $border;
	}

	.offering-simple__list {
		padding-bottom: $spacer;
		border-bottom: $border;
	}
}
