// functionality
button.accordionButton {
	cursor: pointer;
	position: relative;
	// width: 100%;
	text-align: left;
	padding-right: $spacer * 2;

	.icon__chevron--down {
		position: relative;
		top: 3px;
		margin-left: 4px;
	}
}

.accordionButton.on {
	.icon__chevron--down {
		transform: rotate(180deg);
	}
}
