.page-title {
	color: $white;
	line-height: 1.2;
	@include font-size(30);
	@include bp(sm) {
		@include font-size(35);
	}
	@include bp(lg) {
		@include font-size(40);
	}
	@include bp(xl) {
		@include font-size(45);
	}
	@include bp(xxx) {
		@include font-size(55);
	}
}

.page-title--alt {
	color: $color__secondary;
}

.page-title--post {
	color: $color__secondary;
	font-weight: $font__normal;
	line-height: 1.2;
	margin-top: 0;
	@include font-size(20);
	@include bp(sm) {
		@include font-size(22);
	}
	@include bp(lg) {
		@include font-size(26);
		// margin: 0 0 $spacer * 2.5;
	}
	@include bp(xl) {
		@include font-size(28);
	}
	@include bp(xxx) {
		@include font-size(32);
	}
}

.section-title {
	color: $color__secondary;
	font-weight: $font__normal;
	text-align: center;
	margin-bottom: $spacer;
	line-height: 1.2;
	@include font-size(20);
	@include bp(sm) {
		@include font-size(22);
	}
	@include bp(lg) {
		@include font-size(26);
		// margin: 0 0 $spacer * 2.5;
	}
	@include bp(xl) {
		@include font-size(28);
	}
	@include bp(xxx) {
		@include font-size(32);
	}
}

.post-subtitle {
	color: $color__secondary;
	font-weight: $font__normal;
	margin-bottom: $spacer/2;
	@include font-size(16);
	@include bp(sm) {
		@include font-size(18);
	}
	@include bp(lg) {
		// @include font-size(24);
	}
	@include bp(xl) {
		@include font-size(20);
	}
	@include bp(xxx) {
		@include font-size(22);
	}
}

.heading,
.entry-content h3 {
	color: $black;
	font-weight: $font__bold;
	margin-bottom: $spacer;
	@include font-size(18);
	@include bp(sm) {
		@include font-size(20);
	}
	// @include bp(lg) {
	// 	@include font-size(22);
	// 	// margin: 0 0 $spacer * 2.5;
	// }
	// @include bp(xl) {
	// 	@include font-size(26);
	// }
	// @include bp(xxx) {
	// 	@include font-size(30);
	// }
}

.entry-content {
	a {
	}

	h1,
	h2,
	h3,
	h4,
	h5,
	h6 {
	}

	h1:first-child,
	h2:first-child,
	h3:first-child,
	h4:first-child,
	h5:first-child,
	h6:first-child {
		margin-top: 0;
	}

	h2 {
		color: $color__secondary;
		margin-top: $spacer * 2;
		margin-bottom: $spacer/2;
		@include font-size(20);
		@include bp(sm) {
			@include font-size(22);
		}
		@include bp(lg) {
			@include font-size(24);
		}
	}
	h3 {
		margin-top: $spacer * 2;
		margin-bottom: $spacer/2;
	}

	p,
	ul,
	ol {
	}
	blockquote p {
		color: $color__secondary;
		font-style: italic;
		@include font-size(22);
		@include bp(sm) {
			// @include font-size(24);
		}
		@include bp(lg) {
			@include font-size(24);
		}
	}
}
