// -----------------------------
// Team

.team__item {
	@include bp(sm) {
		display: flex;
		justify-content: space-between;
		align-items: center;
	}

	margin-bottom: $spacer * 3;
	padding-bottom: $spacer * 3;
	border-bottom: $border;

	&:last-child {
		border-bottom: none;
	}
}

.team__img {
	flex: 0 1 30%;
}
.team__content {
	flex: 0 1 62%;
}

.team__name {
	margin-bottom: 0;
}
.team__bio {
	margin-bottom: 0;
}
.team__linkedin {
	margin: $spacer/2 0 0;
	a {
		text-decoration: none;
		// text-transform: up;
		@include font-size(16);
		font-weight: $font__bold;
	}
	.icon {
		transform: translateY(4px);
		@include font-size(19);
	}
}
