// -----------------------------
// Offering Detailed

.offering-detailed__item {
	border-bottom: $border;
	text-align: center;
	padding: $spacer * 2 0;

	&:last-child {
		border-bottom: none;
	}

	h3 {
		margin-top: $spacer/2;
	}

	.icon__offerings {
		@include font-size(40);
		color: $color__secondary;
	}
}

.offering-detailed__details {
	ul {
		text-align: left;
		max-width: 700px;
		margin-left: auto;
		margin-right: auto;

		@include bp(md) {
			columns: 2;
		}
	}
	*:last-child {
		margin-bottom: 0;
	}
}
